.App {
  text-align: center;
}

:root {
  --color-main: #f54558;
  --height-navbar: 60px;
  --gray: #cccccc;
  --background-banner: #d7e1e380;
  --background-box-search: #62c1c526;
  --search: #18898d;
  --height-banner: 300px;
  --color-image: #095a88;
  --color-job-box: #62c2c53e;
  --color-yellow: #f9ba19d6;
  --color-gray-job: #898484;
  --color-smoke: rgba(217, 217, 217, 0.13);
  --color-gray-card-user: rgba(217, 217, 217, 0.47);
  --background-hr: #f5f5f5;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.ant-menu-horizontal {
  border-bottom: none !important;
  line-height: 28px;
}

.ant-menu-item-selected {
  color: white !important;
  transition: 0.5s;
}

.ant-menu-item-only-child:hover::after {
  color: white !important;
  border-bottom-color: white !important;
}

.ant-menu-item-selected::after {
  color: white !important;
  border-bottom-color: white !important;
}

.button-search {
  background-color: var(--search);
  color: white;
  font-weight: bold;
  border: none;
  border-radius: 0;
}

.input-custom {
  border-radius: 0;
  outline: none !important;
  border: none !important;
  box-shadow: none !important;
}
